export const checkMobile = (mobile) => {
	return RegExp(/^1[34578]\d{9}$/).test(mobile)
}

export const checkLicense = (str) => {
	return RegExp(
		// /^(([][A-Z](([0-9]{5}[DF])|([DF]([A-HJ-NP-Z0-9])[0-9]{4})))|([][A-Z][A-HJ-NP-Z0-9]{4}[A-HJ-NP-Z0-9]))$/
	/^[A-Z]{1}[A-Z]{1}[A-Z0-9]{4}[A-Z0-9]{1}$/	
	).test(str)
}
export const checkCarNo = (str) => {
	return RegExp(
		/^([][ABCDEFGHJKLMNPQRSTUVWXY][1-9DF][1-9ABCDEFGHJKLMNPQRSTUVWXYZ]\d{3}[1-9DF]|[][ABCDEFGHJKLMNPQRSTUVWXY][\dABCDEFGHJKLNMxPQRSTUVWXYZ]{5})$/
	).test(str)
}
export const getImgSrc = (htmlstr) => {
	let reg = /<img.+?src=('|")?([^'"]+)('|")?(?:\s+|>)/gim
	let imgsrcArr = []
	while (tem = reg.exec(htmlstr)) {
		imgsrcArr.push(tem[2])
	}
	return imgsrcArr
}
